@import url(https://fonts.googleapis.com/css?family=Raleway&display=swap);
.App {
  text-align: center;
  background: url(/Financial-Auralizer/static/media/gas.02a4fc45.png);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  width: 80vw;
  height: 60vh;
  margin: 0 auto;
}

.DataFetch {
  margin: 0 auto;
  padding-top: 30vh;
  background-image: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(255, 255, 255, 0.7)),
    to(rgb(218, 218, 218, 0.3))
  );
  background-image: -webkit-linear-gradient(
    rgba(255, 255, 255, 0.7),
    rgb(218, 218, 218, 0.3)
  );
  background-image: linear-gradient(
    rgba(255, 255, 255, 0.7),
    rgb(218, 218, 218, 0.3)
  );
}

.DataFetch h1 {
  font-size: 5em;
  font-family: "Raleway";
  color: rgb(235, 90, 90);
}

p {
  font-family: "Raleway", sans-serif;
  display: inline-flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 0;
}

.DataSelect {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  flex-wrap: wrap;
  width: 70%;
  margin: 0 auto;
}

