.gasPrice {
  background-color: rgba(250, 238, 221, 0.694);
  font-family: "Raleway";
  border-radius: 4px;
  width: 30%;
  margin: 0 auto;
  margin-top: 20vh;
}

body {
  background-color: rgb(147, 188, 233);
}

